@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  padding: 4rem;
  background-color: #212121;
  font-family: "Inter", sans-serif;
}

.expense-tracker__header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 0 3rem 0;
}

.expense-tracker__title {
  font-family: "Bubblegum Sans", cursive;
  color: #32a9f3;
  text-align: center;
  font-size: 5.6rem;
  font-weight: 600;
  line-height: 1;
}

.expense-item__date {
  color: rgba(255, 255, 255, 0.8);
  min-width: 11rem;
  background-color: #3a3a3a;
  border-right: 0.25px solid rgba(255, 255, 255, 0.2);
  padding: 0.5rem;
  align-self: stretch;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.expense-item__date-month {
  font-size: 1.4rem;
  margin-bottom: 0.3rem;
}

.expense-item__date-year {
  font-size: 1.2rem;
}

.expense-item__date-day {
  font-size: 2rem;
  font-weight: 600;
  margin-top: 0.75rem;
}

.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.6rem;
  font-weight: 500;
  background-color: #323232;
  color: rgba(255, 255, 255, 0.9);
  display: flex;
  overflow: hidden;
}

.expense-item:not(:last-child) {
  margin-bottom: 1.5rem;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1 1;
  margin: 1rem;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1.8rem;
  font-weight: 400;
  text-align: right;
  flex: 1 1;
  color: currentColor;
}

.expense-item__price {
  min-width: 11rem;
  padding: 1.5rem 0;
  font-weight: 600;
  text-align: center;
  color: #fff;
  background-color: #22a2f2;
  margin-bottom: 1rem;
  border-radius: 10px;
  border: 0.5px solid rgba(255, 255, 255, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .expense-item h2 {
    font-size: 2rem;
    margin-left: 1.5rem;
    text-align: left;
  }

  .expense-item__price {
    margin-bottom: 0;
  }
}

.card {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
  border-radius: 12px;
}

.expenses {
  background-color: #242424;
  border: 0.5px solid rgba(255, 255, 255, 0.1);
  padding: 2rem;
  width: 60vw;
  max-width: 75%;
  margin: 0 auto;
}

.new-expense {
  background-color: #242424;
  border: 0.5px solid rgba(255, 255, 255, 0.1);
  padding: 3rem 4rem;
  margin: 4rem auto;
  width: 45vw;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);

  position: relative;
}

.new-expense button {
  font: inherit;
  font-size: 1.6rem;
  font-weight: 400;
  cursor: pointer;
  padding: 1.5rem 5rem;
  border: 2px solid #22a2f2;
  background-color: transparent;
  color: #22a2f2;
  border-radius: 11px;

  transition: all 0.2s;
}

.new-expense button:hover,
.new-expense button:active {
  background-color: #42aff4;
  border-color: #42aff4;
  color: #fff;
}

.new-expense button.alternative {
  color: #08668b;
  border-color: transparent;
  background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active {
  background-color: #49c3f4;
}

.new-expense__form {
  display: flex;
}

.new-expense__controls {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  text-align: left;
  color: rgba(255, 255, 255, 0.8);
  flex: 0 0 55%;
  margin-right: auto;
}

.new-expense__control:not(:last-child) {
  margin-bottom: 2rem;
}

.new-expense__control label {
  font-weight: 500;
  margin-bottom: 0.5rem;
  display: block;
}

.new-expense__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 11px;
  border: none;
  background-color: rgba(255, 255, 255, 0.9);
  width: 100%;
  height: 3.5rem;
  max-width: 100%;
}

.new-expense__control input::-webkit-input-placeholder {
  font-size: 1.2rem;
  color: #3a3a3a;
}

.new-expense__control input:-ms-input-placeholder {
  font-size: 1.2rem;
  color: #3a3a3a;
}

.new-expense__control input::placeholder {
  font-size: 1.2rem;
  color: #3a3a3a;
}

.new-expense__photo {
  /* <a href="https://storyset.com/money">Money illustrations by Storyset</a> */
  position: absolute;
  top: -6rem;
  right: 0;
}

.new-expense__photo img {
  width: 28rem;
  height: 28rem;
}

input[type="date"] {
  color: #3a3a3a;
  font-family: inherit;
  font-size: 1.2rem;
}

.new-expense__actions {
  font-size: 1.4rem;
  margin-top: 3rem;
  font-weight: 600;
  color: #fff;
}

